import React, { useEffect, useState } from 'react';
import { useCustomDispatch, useCustomSelector } from '../../hooks/store';
import { fetchCurrentWeather } from '../../store/thunks/fetchCurrentWeather';
import { Day } from './components/Day/Day';
import { DayInfo } from './components/DayInfo/DayInfo';
import { Days } from './Days/Days';
import './Home.scss'

export const Home = () => {
  const [city, setCity] = useState('honolulu');

  const cityOptions = [
    'Kyiv', 'Honolulu', 'Rome', 'Amsterdam', 'Tel-Aviv', 'Paris', 'Tokyo'
  ]
  const options = cityOptions.map((text, index) => {
    return <option key={index} value={text}>{text}</option>
  })

  const dispatch = useCustomDispatch();
  useEffect(() => {
    dispatch(fetchCurrentWeather(`${city}`));
  }, [city, dispatch]);

  const { weather } = useCustomSelector(state =>
    state.currentWeatherSliceReducer);

  return (
    <div className='home'>
      <div className="home__select--wrap">
        <select
          className='home__select'
          
          name="isCompleted"
          onChange={({ target }) => {
            setCity(target.value);
          }}
        >
          <option value=''>select city</option>
          {options}
        </select>
      </div>
      <div className="home--top">
        <Day weather={weather} />
        <DayInfo weather={weather} />
      </div>

      <div className="home--bottom">
        <Days />
      </div>
    </div>
  )
}
