import React from 'react';
import { Item } from '../DayInfo/DayInfo';
import './DayInfoItem.scss';

interface Props{
  item: Item
}

const DayInfoItem = ({item}: Props) => {
  const {img, name, value} = item;

  return (
    <div className='day-info-item'>
      <div className="day-info-item__wrap">
        <div className={img}></div>
        <div className="day-info-item__name">{name}</div>
      </div>
      <div className="day-info-item__info">{value}</div>
    </div>
  )
}

export default DayInfoItem