import React from 'react';
import './Days.scss';

interface Props {}

export const Tabs = (props: Props) => {
  const tabs = [
    {
      value: '1 week',
    },
    {
      value: '10 days',
    },
    {
      value: 'Month',
    },
  ];
  return (
    <div className='days__tabs'>
      <div className='days__tabs__wrapper'>
        {tabs.map(tab => (
          <div className='days__tabs--tab' key={tab.value}>
            {tab.value}
          </div>
        ))}
      </div>
      <div className='days__tabs--tab'>Cancel</div>
    </div>
  );
};
