import React from 'react';
import cloud from '../../../../images/cloud.png';
import { Weather } from '../../../../store/types/types';
import DayInfoItem from '../DayInfoItem/DayInfoItem';
import './DayInfo.scss';

interface Props {
  weather: Weather;
}

export interface Item {
  img: string,
  name: string,
  value: string
}

export const DayInfo = ({ weather }: Props) => {
  const items = [
    {
      img: 'temp',
      name: 'Temperature',
      value: `${Math.floor(weather.main.temp)} - feels like ${Math.floor(weather.main.feels_like)}`
    },

    {
      img: 'pres',
      name: 'Pressure',
      value: `${weather.main.pressure} kPa`
    },

    {
      img: 'prec',
      name: 'Precipitation',
      value: `${weather.main.humidity} %`
    },

    {
      img: 'wind',
      name: 'Wind',
      value: `${weather.weather[0].description} % ${console.log(weather.weather)}`
    }
  ]
  return (
    <div className='day-info'>
      <div className="day-info__items">
        {items.map((item:Item) => (<DayInfoItem item={item} key={item.name}/>))}
      </div>
      <img src={cloud} alt="cloud" className="day-info__image" />
    </div>
  )
}
